    title: 'About Me - Azpact'
  },
  data:()=>({
    img:img
  }),
  methods: {
    async to(id) {
      let element = `#${id}`
      const elementOffsetTop = await $(element).offset().top;
      $("html, body").animate({ scrollTop: elementOffsetTop }, 900);
    }
  },
  mounted() {
    let vm = this;
    let s = this.$route.query.s;
    let id = this.$route.query.id;

    function toId(s, id) {
      switch (s) {
        case "1":
          vm.to(id)
          break;
        default:
          // console.log('Not any id')
          break;
      }
    }
    toId(s, id);
  },
  components: {
    VRaisedBtn
  }
};
</script>
